import "./styles.css";

let addTodo = document.getElementById("addTodo");
let todoInput = document.getElementById("todoInput");
let todoList = document.getElementById("todoListUl");
let todayDate = document.getElementById("todayDate");
let deadLine = document.getElementById("deadLine");

let today = new Date();
let fullDate =
  today.getFullYear() + "/" + (today.getMonth() + 1) + "/" + today.getDate();
todayDate.append(fullDate);

function addingTodo() {
  let liTodo = document.createElement("li");
  let Ptodo = document.createElement("p");
  let checkTodoBtn = document.createElement("button");
  let deleteTodoBtn = document.createElement("button");

  deleteTodoBtn.style.width = "40px";
  deleteTodoBtn.style.height = "40px";
  deleteTodoBtn.innerHTML = "ðŸ—‘";
  deleteTodoBtn.style.fontSize = "36px";
  deleteTodoBtn.style.border = "none";
  deleteTodoBtn.style.paddingTop = "5px";
  deleteTodoBtn.style.backgroundColor = "transparent";
  deleteTodoBtn.style.outline = "none";
  deleteTodoBtn.style.paddingRight = "25px";
  deleteTodoBtn.style.marginRight = "25px";
  deleteTodoBtn.style.marginBottom = "10px";

  checkTodoBtn.style.width = "40px";
  checkTodoBtn.style.height = "40px";
  checkTodoBtn.style.borderRadius = "50%";
  checkTodoBtn.style.outline = "none";
  checkTodoBtn.style.margin = "10px 40px";
  checkTodoBtn.style.paddingRight = "25px";
  checkTodoBtn.style.paddingLeft = "10px";
  checkTodoBtn.style.boxSizing = "border-box";
  checkTodoBtn.style.backgroundColor = "#e2bf9f";

  Ptodo.style.fontSize = "22px";
  Ptodo.style.paddingRight = "22px";
  Ptodo.style.paddingTop = "5px";
  Ptodo.style.wordBreak = "break-word";
  Ptodo.style.margin = "auto";
  Ptodo.style.boxSizing = "border-box";
  Ptodo.innerText = `${todoInput.value} ${deadLine.value}`;

  liTodo.style.listStyleType = "none";
  liTodo.style.display = "flex";
  liTodo.style.flexDirection = "row";
  liTodo.style.backgroundColor = "#fff0f5";
  liTodo.style.margin = "5px auto";
  liTodo.style.borderRadius = "4px";

  if (todoInput.value !== "" && deadLine.value !== "") {
    liTodo.append(Ptodo);
    liTodo.append(deleteTodoBtn);
    liTodo.insertAdjacentElement("afterbegin", checkTodoBtn);
    todoList.append(liTodo);
  }

  function checkedTodo() {
    checkTodoBtn.style.backgroundColor = "rgb(150, 10, 10)";
    checkTodoBtn.style.borderTopColor = "#c0a286";
    checkTodoBtn.style.color = "honeydew";
    checkTodoBtn.style.fontSize = "22px";
    checkTodoBtn.style.textAlign = "center";
    checkTodoBtn.innerHTML = "âœ”";
    liTodo.style.backgroundColor = "#cdaf95";
    Ptodo.style.textDecoration = "line-through";
  }

  function deletingTodos() {
    liTodo.remove();
  }

  checkTodoBtn.addEventListener("click", checkedTodo);
  deleteTodoBtn.addEventListener("click", deletingTodos);
}

addTodo.addEventListener("click", addingTodo);
